.info{
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-top: 20px;
    &-quest{
        margin-top: 0;
        font-family: var(--font-family-montsterrat);
        text-align: center;
    }
    &-search{
       width: 80%;
       display: block;
       margin: 0 auto;
       height: 40px;
       border-radius: 25px;
       border: none;
       color: #606f81;
       padding-left: 15px;
       background-image: url(../images/research.png);
       background-repeat: no-repeat;
       background-size: inherit;
       background-position: right;
       background-size: 20px 20px; 
    }
}