.banner{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: var(--font-family-montsterrat);

    &-title{
        margin-top: 40px;
        margin-bottom: 0;
        text-align: center;
        color:var(--white) ;
        
        font-size: 42px;
        font-weight: 600;
    }
    &-title__text{
        color: var(--pink);
        font-weight: 700;
    }
    &-text{
        text-align: center;
        margin-top: 7px;
        color: var(--white);
        font-weight: 100;
        opacity: 0.7;
        font-size: 14px;
        letter-spacing: 0.7px;
    }
    &-search__batton{
        margin-top: 20px;
        font-size: 18px;
        align-items: center;
        justify-content: center;
        display: flex;
        height: 40px;
        width: 200px;
        background-color: var(--yellow);
        border-radius:25px ;
        
    }
    &-search__batton>a{
        color: var(--black);
    }
    &-description{
        margin-top: 50px;
        color: var(--white);
        letter-spacing: 1px;
        font-weight: 100;
    }
}
.banner-slider-wrap{
    
    justify-content: center;
    width: 250px;
    height: 270px;
    overflow: hidden;
    position: relative;
    
}
.banner-slider{
    justify-content: center;
    width: 250px;
    height: 270px;
    position: relative;
}
.cards{
    opacity: 1;
    position: absolute;
    margin-top: 5px;
    display:flex;
    flex-direction: column;
    // justify-content: center;
    align-items: center;
    
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 250px;
    height: 270px;
    color: var(--white);
    letter-spacing: 1px;
    transition: all ease 1s;
    &-image__1{
        background-image: url(../images/bat.jpg);
    }
    &-image__2{
        background-image: url(../images/lake.jpg);
    }
    &-image__3{
        background-image: url(../images/gor.jpg);
    }
    &-image__4{
        background-image: url(../images/vylkove.jpg);
    }
    &-image__5{
        background-image: url(../images/vodospad.jpg);
    }
    &__title{
        background:rgba(0, 0, 0, 0.3);
        border-radius: 10px;
        font-size: 36px;
        margin-top: 90px;
        font-weight: 600;
        margin-bottom: 0;
        padding: 5px;
    }
    &__text{
        display: none;
        font-size: 12px;
        background:rgba(237, 105, 80, 0.3) ;
        padding:1px 4px;
        border-radius: 5px;
    }
}
.banner-search__batton__slider{
    margin-top: 50px;
    
    width: 150px;
    height: 30px;
    font-size: 10px;
    text-transform: uppercase;
    color: var(--black);
    bottom: 0;
    
}
.banner-slider .opacity0{
    opacity: 0;
}