
.logo {
    
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    color: var(--white);
   
    &-wrapper{
        display: flex;
    }
   
    &-img{
        margin-top: 7px;
        margin-left: 7px;
        height: 45px;
        width: 45px;
        background-image: url(../images/Logo.png);
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        display: block;
    }
   
    &-title{
            font-family: var(--font-family-montsterrat);
            font-size: 30px;
            font-weight: 500;
            margin: 10px 10px 15px 10px;            
    }
    
    &-search{
        margin: 10px 10px;
        
        display: flex;
        
    }
    &-search__img{
        background: none;
        border: none;
        background-image: url(../images/research.png);
        width: 40px;
        height: 35px;
        background-repeat: no-repeat;
        transition-duration: 850ms;
        &:hover{
            height: 40px;
            align-items: center;
            display: flex;
            background-size: inherit;
            background-position: left;
            background-position: 9px;
            background-color: rgba($color: #fff, $alpha: 0.3);
            
            width: 150px;
            border-radius: 25px;
            transform:  transform(-10em, );
        }
    }
}
